/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
:host {
  display: flex;
  flex-direction: column;
  font-family: Roboto, sans-serif; }
  :host.vdl-tab-group-inverted-header {
    flex-direction: column-reverse; }

.vdl-tab-label {
  line-height: 40px;
  height: 40px;
  margin: 0;
  padding: 0 15px;
  font-size: 16px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  cursor: pointer;
  box-sizing: border-box;
  color: #414142;
  min-width: 160px;
  text-align: center;
  border-radius: 3px;
  transition: border-bottom 4ms ease-in-out 3ms;
  position: relative; }
  .vdl-tab-label:focus, .vdl-tab-label.focus {
    outline: none; }
  .vdl-tab-label:hover, .vdl-tab-label.hover {
    background-color: #f3f3f3; }
  .vdl-tab-label.vdl-tab-link-active, .vdl-tab-label.vdl-tab-label-active {
    color: #005a99; }
  .vdl-tab-label.vdl-tab-disabled {
    cursor: default;
    pointer-events: none;
    color: #a5a5a5; }

@media (max-width: 600px) {
  .vdl-tab-label {
    min-width: 72px; } }

:host[vdl-stretch-tabs] .vdl-tab-label {
  flex-basis: 0;
  flex-grow: 1; }

.vdl-tab-body-wrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  transition: height 500ms cubic-bezier(0.35, 0, 0.25, 1); }

vdl-tab-body {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  overflow: hidden; }
  vdl-tab-body.vdl-tab-body-active {
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 1;
    flex-grow: 1; }
  :host.vdl-tab-group-dynamic-height vdl-tab-body.vdl-tab-body-active {
    overflow-y: hidden; }
